.LoadingIndicator {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
}

    .LoadingIndicator__wrapper {
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;

        width: auto;
        height: 14px;

        font-size: 0;
        line-height: 1em;
    }

    .LoadingIndicator__item {
        display: inline-block;
        vertical-align: middle;

        width: 14px;
        height: 14px;
        background: rgba(54, 181, 255, 0.5);

        -webkit-animation: circleBounceLoader 1.4s infinite ease-in-out both;

                animation: circleBounceLoader 1.4s infinite ease-in-out both;
        margin-right: 7px;

        border-radius: 100%
    }

    .LoadingIndicator__item:nth-child(1) {
    -webkit-animation-delay: -0.32s;
            animation-delay: -0.32s;
}

    .LoadingIndicator__item:nth-child(2) {
    -webkit-animation-delay: -0.16s;
            animation-delay: -0.16s;
}

    .LoadingIndicator__item:nth-child(3) {
    margin-right: 0;
}

@-webkit-keyframes circleBounceLoader {
    0%, 80%, 100% {
        -webkit-transform: scale(0);
                transform: scale(0)
    }

    40% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes circleBounceLoader {
    0%, 80%, 100% {
        -webkit-transform: scale(0);
                transform: scale(0)
    }

    40% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


